import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import {VehiculoestadosComponent } from './vehiculoestados.component';
import {VehiculoestadosTableComponent } from './components/vehiculoestados-table/vehiculoestados-table.component';
// noinspection TypeScriptValidateTypes
export const routes: Routes = [
  {
    path: '',
    component: VehiculoestadosComponent,
    children: [
      { path: 'vehiculoestados-table', component: VehiculoestadosTableComponent }
    ]
    }
  ];
export const routing: ModuleWithProviders = RouterModule.forChild(routes);



