import { AuthService } from './../../../../shared/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DialogService } from 'ng2-bootstrap-modal';
import { ToastrService } from 'ngx-toastr';
import { VehiculoestadosInterface } from './vehiculoestados.interface';
import { VehiculoestadosResponseInterface } from './vehiculoestados-response.interface';
import { Component, OnInit } from '@angular/core';
import { VehiculoestadosService } from './vehiculoestados.service';
import { VehiculoestadosAddModalComponent } from './vehiculoestados-add-modal/vehiculoestados-add-modal.component';
import { VehiculoestadosEditModalComponent } from './vehiculoestados-edit-modal/vehiculoestados-edit-modal.component';

@Component({
selector: 'vehiculoestados-table',
templateUrl: './vehiculoestados-table.html',
styleUrls: ['./vehiculoestados-table.scss'],
})
export class VehiculoestadosTableComponent implements OnInit {
    data;
    filterQuery = '';
    rowsOnPage = 10;
    sortBy = 'idvehiculoestado';
    sortOrder = 'asc';
    backpage: boolean;

    // Permisos en vista
    updateable: boolean = false;
    deleteable: boolean = false;
    writeable: boolean = false;

    constructor(
      private service: VehiculoestadosService, 
      private toastrService: ToastrService, 
      private dialogService: DialogService, 
      private authService: AuthService, 
      private route: ActivatedRoute, 
      private router: Router) {

      // Buscar permisos del usuario en el módulo
      const user = this.authService.useJwtHelper();
      
      if (user.super) {
        this.updateable = true;
        this.deleteable = true;
        this.writeable = true;
      } else {
        const userModules = this.authService.getUserModules();
        if (userModules[0]) {
          for (const element in userModules) {
            if (userModules[element].path === '/pages/vehiculoestados') {
              this.updateable = userModules[element].updateable;
              this.deleteable = userModules[element].deleteable;
              this.writeable = userModules[element].writeable;
            }
          }
        }
      }
    }
    ngOnInit() {
      this.refill();
    }
    refill() { 
      this.route.params.subscribe(params => {
        if (params['idestadoactividad'] !== undefined) {
          const idestadoactividad = +params['idestadoactividad'];
          this.findByIdEstadoactividad(idestadoactividad);
          this.backpage = true;
        }
        if (params['idvehiculo'] !== undefined) {
          const idvehiculo = +params['idvehiculo'];
          this.findByIdVehiculo(idvehiculo);
          this.backpage = true;
        }
        if (!this.backpage) {
          this.getAll();
        }
      });
    }
    private findByIdEstadoactividad(id: number): void {
      this.service
        .findByIdEstadoactividad(id)
        .subscribe(
            (data: VehiculoestadosResponseInterface) => {
                if (data.success) {
                  this.data = data.result;
                } else {
                  this.toastrService.error(data.message);
                }
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }
    private findByIdVehiculo(id: number): void {
      this.service
        .findByIdVehiculo(id)
        .subscribe(
            (data: VehiculoestadosResponseInterface) => {
                if (data.success) {
                  this.data = data.result;
                } else {
                  this.toastrService.error(data.message);
                }
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    }
    backPage() {
        window.history.back();
    }
    addModalShow() {
      const disposable = this.dialogService.addDialog(VehiculoestadosAddModalComponent)
      .subscribe( data => {
          if (data) {
              this.showToast(data);
          }
      });
    }
    editModalShow(vehiculoestados: VehiculoestadosInterface) {
      const disposable = this.dialogService.addDialog(VehiculoestadosEditModalComponent, vehiculoestados)
      .subscribe( data => {
          if (data) {
              this.showToast(data);
          }
      },
      error => console.log(error),
      () => console.log('Modified complete'));
    }
    onDeleteConfirm(event, item): void {
      if (window.confirm('¿Estas seguro de querer eliminar este registro?')) {
          this.service.remove(item.idvehiculoestado)
          .subscribe(
              (data) => this.showToast(data),
              error => console.log(error),
              () => console.log('Delete completed')
          );
      } else {
          console.log('item cancelado');
      }
    }
    showToast(result) {
      if (result.success) {
        this.toastrService.success(result.message);
        this.refill();
      } else {
        this.toastrService.error(result.message);
      }
    }
    private getAll(): void {
      this.service
        .all()
        .subscribe(
            (data: VehiculoestadosResponseInterface) =>  {
                if (data.success) {
                  this.data = data.result;
                } else {
                  this.toastrService.error(data.message);
                }
            },
            error => console.log(error),
            () => console.log('Get all Items complete'))
    } 
  }



